<template>
  <div class="app flex-row align-items-center">
    <div class="container">
      <b-row class="justify-content-center">
        <b-col md="8">
          <b-card-group>
            <b-card no-body class="p-4">
              <b-card-body>
                <b-form >
                  <h1>Login</h1>
                  <p class="text-muted">Sign In to your account</p>

                  <b-input-group class="mb-3">
                    <b-input-group-prepend>
                      <b-input-group-text>
                        <i class="icon-user"></i>
                      </b-input-group-text>
                    </b-input-group-prepend>
                    <input
                      class="form-control form-control-lg"
                      type="text"
                      v-model="email"
                      placeholder="Email"
                    />
                  </b-input-group>

                  <b-input-group class="mb-4">
                    <b-input-group-prepend>
                      <b-input-group-text>
                        <i class="icon-lock"></i>
                      </b-input-group-text>
                    </b-input-group-prepend>
                    <input
                      class="form-control form-control-lg"
                      type="password"
                      v-model="passwords"
                      placeholder="Password"
                    />
                  </b-input-group>

                  <b-row>
                    <b-col cols="6">
                      <div id="login">
                        <b-button v-on:click="onSubmit(email,passwords)" variant="primary" class="px-4">Login</b-button>
                      </div>
                    </b-col>
                    <b-col cols="6" class="text-right">
                      <b-button variant="link" class="px-0">Forgot password?</b-button>
                    </b-col>
                  </b-row>
