<script>
import { mapState } from "vuex";
import { LOGIN } from "@/store/actions.type";
export default {
  name: "RwvLogin",
  data() {
    return {
      email: null,
      passwords: null
    };
  },
  methods: {
    onSubmit(email,passwords) {
      alert(email);
      this.$store
      this.$router.push({ name: "HR" })
        .dispatch(LOGIN, { email, passwords })
        .then(() => this.$router.push({ name: "HR" }));
    }
  },
    computed: {
    ...mapState({
      errors: state => state.auth.errors
    })
  }
};
</script>

